@import url(https://fonts.googleapis.com/css2?family=Shadows+Into+Light&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}


*{
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: 'Shadows Into Light';
}

body {
  background-color: #44E2E2;
}


nav {
    background-color: #101524;
}

ul {
    display: flex;
    align-items: center;
    justify-content: center;
    flex-wrap: wrap;
    
}

ul li {
    list-style-type: none;
    margin: 0 2rem;
}

ul li:hover {
background-color: #44E2E2;
}

ul a{
    display: inline-block;
    text-decoration: none;
    color: #fff;
    font-weight: 900; 
    padding: 20px 20px;
}


.header {
    text-align: center;
    font-weight: 900;
    padding: 20px 0;
}
.container {
    height:auto;
    width: 100%;
    display: inline-flex;
    justify-content: space-around;
    flex-wrap: wrap;
    padding: 0 50px;
}

.card {
    background-color: #101524;
    width: 350px;
    height: 320px;
    border-radius: 10px;
    padding: 1rem;
    color: #fff;
    font-weight: 600;
    margin-bottom: 20px;
    position: relative;
    box-shadow: 0px 0px 15px -1px #000000;
}

.card:hover{
    cursor: pointer;
}

.card p:not(.desc){
    margin-bottom: 5px;
    font-size: 1.2rem;

}

img {
    width: 100%;
    height: 250px;
    border-radius: 10px;
}

.desc{
    opacity: 0;
    position: absolute;
    bottom: 0%;
    left: 5%;
    color: #fff;
    padding: 0 20px 0 3px;
    font-size: 1.03rem;
    transition: all 0.5s ease-in-out;
}

.card:hover .desc{
    opacity: 1;
    bottom: 8%;
    transition: all 0.5s ease-in-out;
}


